import { http } from './config'

export default {
    //Logar
    login: (usuario) => {
        return http.post("customers/autenticate", usuario)
    },
    //Cadastro de usuário
    cadastro: (usuario) => {
        return http.post("customers", usuario)
    },
    //Listar usuários
    listarUsuarios:() => {
        return http.get('customers/users')
    },
    //Listar arquivos
    getFiles: (id) => {
        return http.get('customers/files/'+id)
    },
    //Download file
    fileDownload: (id) => {
        return http.get('file/download/'+id)
    },
   //Buscar usuario por id
   getUser: (id) => {
       return http.get('customers/user/'+id)
   }
}