import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import Login from './views/login.vue'

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      children: [
        { path: 'users', name:'users',  component: () => import('./views/About.vue')},
        { path: 'user/:id', name:'user',  component: () => import('./components/user.vue')},
        { path: 'client', name:'client',  component: () => import('./components/client.vue')}
      ]
    },
    {
      path: '/about',
      name: 'about',
      component: () => import('./views/About.vue'),
    },
    {
      path: '/cadastro',
      name: 'cadastro',
      component: () => import('./components/cadastroUser.vue'),
    },
    {
      path:'/login',
      name: 'login',
      component: Login
    }
  ],
});
