<template>
<v-app id="inspire">
    <v-navigation-drawer v-model="drawer" fixed app>
        <v-list dense>
            <v-list-tile @click="listUsers">
                <v-list-tile-action>
                    <v-icon>home</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                    <v-list-tile-title>Home</v-list-tile-title>
                </v-list-tile-content>
            </v-list-tile>
            <v-list-tile @click="logout">
                <v-list-tile-action>
                    <v-icon>logout</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                    <v-list-tile-title>Sair</v-list-tile-title>
                </v-list-tile-content>
            </v-list-tile>
        </v-list>
    </v-navigation-drawer>
    <v-toolbar color="indigo" dark fixed app>
        <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
        <v-toolbar-title>ROMA Contabilidade Online</v-toolbar-title>
    </v-toolbar>

    <v-container fluid fill-height>
        <v-layout>
            <v-flex xs10 sm12 md12>
              <transition name="slide">
                <router-view></router-view>
              </transition>
            </v-flex>
        </v-layout>
    </v-container>

    <v-footer color="indigo" app>
        <span class="white--text">&copy; Órbita Tecnologia</span>
    </v-footer>
</v-app>
</template>

<script>
name: 'toolbar';
export default {
    data: () => ({
        drawer: null,
        user: {}
    }),

    props: {
        source: String
    },
    methods: {
        listUsers() {
            if (this.user.data.role === 'admin') {
                this.$router.push("/users");
            } else {
                this.$router.push("/client");
            }
        },
        logout() {
            this.$cookie.set("user", null, 1);
            this.$router.push("/login");
        }
    },
    mounted() {
        this.user = JSON.parse(this.$cookie.get('user'));
        if (this.user == null || this.user.data.nome === '') {
            this.$router.push("/login")
        } 
        if (this.user.data.role === 'admin') {
                this.$router.push("/users");
            } else {
                this.$router.push("/client");
            }
    }
}
</script>

<style>
  .router-anim-enter-active {
    animation: coming 1s;
    animation-delay: .5s;
    opacity: 0;
  }
  
  .router-anim-leave-active {
    animation: going 1s;
  }
  
  @keyframes going {
    from {
      transform: translateX(0);
    }
    to {
      transform: translateX(-50px);
      opacity: 0;
    }
  }
  
  @keyframes coming {
    from {
      transform: translateX(-50px);
      opacity: 0;
    }
    to {
      transform: translateX(0);
      opacity: 1;
    }
  }
  </style>
